import React, { useEffect, useState } from "react";
import Original from "./Original";

const App = () => {
    const [show, setShow] = useState(false);
    useEffect(() => {
        !localStorage.getItem("Xdol!671") && setShow(true);
    }, []);
    return (
        <>
            {show && <Original />}
        </>
    );
};

export default App;
