import React, { useEffect, useState } from "react";
import Wheel from "lottery-wheel";
import styled from "styled-components";
import Axios from "axios";
import { motion } from "framer-motion";

const Original = () => {
    const [radius, setRadius] = useState(0);
    const [buttonWidth, setButtonWidth] = useState(0);
    const [width, setWidth] = useState(0);
    const [height, setHeight] = useState(0);
    const [showForm, setForm] = useState(false);
    const [product, setProduct] = useState(null);
    const [name, setName] = useState('');
    const [phone, setPhone] = useState('');
    const [image, setImage] = useState('')

    useEffect(() => {
        window.alert('Хэрэглэгч та азын хүрдийг зөвхөн нэг удаа эргүүлэх боломжтойг анхаарна уу.');
        const goSyncronous = async () => {

            let res = await Axios('https://sugalaa.herokuapp.com/lottery-wins?_sort=createdAt:DESC');
            let speed = await Axios('https://sugalaa.herokuapp.com/settings');
            let generatingDataToDraw = [];
            res.data.length && res.data.map((elem,i)=>{
                let ref = Number(elem.Reference);
                let matching = res.data[ref-1];
                generatingDataToDraw.push({text: elem.Serial, name: elem.Name, ref: elem.Reference, chance: matching.RemainingStock < 1 ? 0 : matching.Chance})
            })

            let container = document.querySelector('.lottocontainer');
            let width = container.offsetWidth;
            let height = container.offsetHeight;
            let radius = width * 0.15;
            let buttonWidth = width * 0.07;

            new Wheel({
                el: document.getElementById("wheel1"),
                data: generatingDataToDraw,
                radius,
                buttonWidth,
                duration: Number(speed.data.Speed + '000'),
                image: {
                    turntable: "https://i.imgur.com/To3E48t.png",
                    button: "https://i.imgur.com/i4XL4wY.png",
                    offset: -10,
                },
                onSuccess(data) {
                    placeOperation(data, res.data);
                },
            });
            window.addEventListener("resize", ()=>window.location.reload());
            setRadius(radius);
            setWidth(width);
            setHeight(height);
            setButtonWidth(buttonWidth);
        }
        goSyncronous();

    }, []);

    const placeOperation = async (data, collection) => {
        if(Number(data.text) === 16) {
            // localStorage.setItem('Xdol!671', '!@E!@GRTM*');
            window.alert('Та дараа дахин азаа үзээрэй! Баярлалаа.');
            return document.location.reload();
        }
        let current = collection[Number(data.text) - 1];
        // console.log(`text: ${data.text}, name: ${data.name}, ref: ${data.ref}`);
        // console.log(current,'heey')
        await Axios.put(`https://sugalaa.herokuapp.com/lottery-wins/${current._id}`, {RemainingStock: current.RemainingStock -1})
        setForm(true);
        setProduct(data.name);
        setImage('https://sugalaa.herokuapp.com'+current.Image.url);
        // localStorage.setItem('Xdol!671', '!@E!@GRTM*');
    }
    const formSubmit = async () => {
        if(phone.length < 8){
           return window.alert('Утасны дугаараа зөв оруулна уу!')
        }
        if(name.length <2){
            return window.alert('Та нэрээ оруулна уу!');
        }
        let dateObj = new Date();
        let Ognoo = dateObj.getFullYear() + '.' + (dateObj.getMonth() + 1) + '.' + (dateObj.getDate())
        // console.log(name, phone, product, Ognoo);
        await Axios.post(`https://sugalaa.herokuapp.com/winners`, {Ner: name, Utas: phone, Baraa: product, Ognoo});
        window.alert('Амжилт хүсье!');
        document.location.reload();
    }
    return (
        <Wrapper width={width} height={height} radius={radius} buttonWidth={buttonWidth}>
            <div className="lottocontainer">
                <img id="arrow" src="https://i.imgur.com/dSIibwI.png" />
                <div id="wheel1"></div>
            </div>
            <div className="fade"></div>
            {showForm &&
            <motion.div initial={{opacity:0, y:60}} animate={{opacity:1, x:0, transition: {type: "spring"}}} id="form" style={{backgroundImage:'url(https://i.imgur.com/H8yhPM9.gif)'}}>
                <img style={{height:300, margin:'0px auto', display:'block'}} src={image} />
                <label><span>Таны хонжвор:</span><span>{product}</span></label>
                <input type="text" name="name" placeholder="Нэр" value={name} onChange={(e)=>setName(e.target.value)} />
                <input type="text" name="phone" placeholder="Утас" value={phone} onChange={(e)=>setPhone(e.target.value)} />
                {/* <input type="text" name="phone" disabled placeholder={`Таны хонжвор: ${product}`} /> */}
                <button onClick={formSubmit}>Илгээх</button>
            </motion.div>
            }
        </Wrapper>
    );
};

export default Original;

const Wrapper = styled.div `
    position:fixed;
    top:0;
    right:0;
    bottom:0;
    left:0;
    z-index:999;
    .fade{
        position:absolute;
        left:0;
        top:0;
        right:0;
        bottom:0;
        background: rgba(0,0,0,0.6);
        z-index:1000;
    }
    .lottocontainer{
        background:white;
        position:absolute;
        z-index:1001;
        margin-left:10vw;
        margin-top:2vw;
        width:80vw;
        height:45vw;
        background-image: url(https://i.imgur.com/aWorWBt.png);
        background-size: 100% 100%;
        #wheel1{
            width: calc(${props => props.radius * 2}px);
            height: calc(${props => props.radius * 2}px);
            margin-left: calc(${props => props.width / 2}px - ${props => props.radius}px);
            margin-top: calc(${props => props.height / 2}px - ${props => props.radius}px);
        }
        #arrow{
            position:absolute;
            width:${props => props.width * 0.085}px;
            margin-left: calc(${props => props.width / 2}px - ${props => props.width * 0.048}px);
            margin-top: calc(${props => props.height / 2.08}px - ${props => props.radius}px);
        }
    }
    #form{
        position:absolute;
        background:white;
        z-index:1002;
        padding:30px;
        width:300px;
        left:50%;
        margin-left:-180px;
        ${'' /* margin-top: ${props => props.height / 2 - 150}px; */}
        text-align:center;
        background-size: 100% 300px;
        background-repeat: no-repeat;
        input{
            border:none;
            margin-top:15px;
            border-bottom:1px solid rgba(0,0,0,0.1);
            width:100%;
            padding:10px;
        }
        label{
            display:flex;
            font-family:Arial;
            span{
                &:first-child{
                    margin-right:10px;
                }
                &:last-child{
                    font-weight:bold;
                }
            }
        }
        button{
            display:block;
            width:100%;
            border:none;
            margin-top:30px;
            padding:15px;
            &:hover{
                cursor:pointer;
            }
        }
    }
`

const sanitize = (number) => {
    switch(Number(number)){
        case 1: return 1;
        case 2: return 16;
        case 3: return 15;
        case 4: return 14;
        case 5: return 13;
        case 6: return 12;
        case 7: return 11;
        case 8: return 10;
        case 9: return 9;
        case 10: return 8;
        case 11: return 7;
        case 12: return 6;
        case 13: return 5;
        case 14: return 4;
        case 15: return 3;
        case 16: return 2;
        default:
            return 'fuck';
    }
}